* {
  margin: 0;
  padding: 0;
  font-size: 100%;
  box-sizing: border-box;
  transition: 0.3s;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  
  &:hover {
    .button-panel {
      width: 200px;
      margin-top: 50px;
      opacity: 1;
    }
  }
  
  .title {
    font-size: 222%;
    color: #222d;
  }
  
  .button-panel {
    position: relative;
    width: 50px;
    margin-top: -30px;
    opacity: 0;
    
    .button {
      display: inline-block;
      padding: 1px 11px;
      font-size: 132%;
      background-color: #333a;
      text-align: center;
      transform: skew(30deg);
      
      .text {
        transform: skew(-30deg);
      }
    }
    
    .button:nth-child(2) {
      position: absolute;
      right: 0;
      transform: skew(-30deg);
      
      .text {
        transform: skew(30deg);
      }
    }
  }
}

.bottom-right {
  display: flex;
  flex-direction: column;
  overflow: hidden;

  &:hover {
    
    .github {
      height: 20%;
    }
    
    .contact {
      height: 20%;
    }
  }
  
  .background {
    position: absolute;
    width: 150%;
    height: 190%;
    transform: rotateZ(10deg);
  }
  
  .github {
    position: relative;
    height: 50%;
    
    &:hover {
      height: 80%;
    }
    
    .background {
      bottom: 0;
      left: -25%;
      background-color: gold;
      z-index: -1;
    }
  }
  
  .contact {
    position: relative;
    height: 50%;
    
    &:hover {
      height: 80%;
    }
    
    .background {
      top: 0;
      left: -25%;
      background-color: #03a08e;
      z-index: -1;
    }
  }
}